h1#project{
    font-size: 1.8em;
    color: #000000;
    margin-left: 10px;
    font-style: italic;
}

h2#proj {
    text-align: center;
    padding: 40px 0px 0px 0px;
    font-size: 2.6em;
    text-align: center;
    font-weight: bold;
    text-shadow: 4px 4px 4px 4px white;
}

p#project{
    text-align: left;
    font-size: 1.2em;

}

p#title{
    text-align: left;
    font-weight: bold;
    min-width: 100%;
    font-size: 1.2em;
    margin-left: 0px;
    margin-top: 0%;
}

img{
    margin-right: 10px;
