}

.containerproject {
    display: flex;
    align-items: top;
    margin-left: 0px;
}

li{
    text-align: left;
}

div#box{
    box-shadow: rgba(1, 71, 57, 0.24) 0px 3px 8px;
    width: 95%;
    /* height: 400px; */
    height: fit-content;
    display: flex;
    align-items: top;
    margin-left: 25px;
    margin-right: 25px;
    margin-top: 25px;
    margin-bottom: 25px;
    border-radius: 10px;
}

div#box:hover{
    box-shadow: rgba(1, 71, 57, 0.24) 2px 6px 12px;

}


